import React from 'react';

import Layout from '../components/layout';
import SEO from '../components/seo';

import content from '../../site/content/team.json';

interface Props {
  pageContext: {
    locale: string;
    canonical: boolean;
    alternatePages: {
      slug: string;
      locale: string;
    }[];
  };
}

const Home = ({ pageContext }: Props) => {
  const { locale, canonical, alternatePages } = pageContext;
  return (
    <>
      <SEO
        title={content[locale].seo.title}
        description={content[locale].seo.description}
        image={content[locale].seo.image}
        locale={locale}
        canonical={canonical}
        alternatePages={alternatePages}
      />
      <Layout locale={locale}>
        <div className="container pt-32 md:pt-36">
          <h1 className="text-5xl sm:text-6xl md:text-9xl text-grey">
            {content[locale].title}{' '}
            <b className="text-white">{content[locale].keyword}</b>
          </h1>
          <div className="flex flex-col items-center justify-center text-center py-8 md:py-24 md:flex-row">
            {content[locale].members.map(member => (
              <a
                className="bg-gradient-to-b from-blue to-blue-dark shadow p-4 my-8 rounded block w-56 h-72 transition duration-500 ease-in-out hover:ring-2 hover:ring-white md:mx-4"
                href={member.linkedin_url}
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  src={member.photo}
                  alt={member.name}
                  className="rounded-full h-32 w-32 object-cover mx-auto p-2"
                />
                <h3 className="text-xl font-semibold text-white">{member.name}</h3>
                <p className="text-orange">{member.role}</p>
                <p className="text-grey text-sm mt-4">{member.subrole}</p>
              </a>
            ))}
          </div>
        </div>
      </Layout>
    </>
  );
};

export default Home;
